import { CommonModule } from '@angular/common';
import {
  Component, CUSTOM_ELEMENTS_SCHEMA,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Subject, takeUntil } from 'rxjs';
import { ColorPickerComponent } from '../color-picker/color-picker.component';
import { DrawingGridComponent } from '../drawing-grid/drawing-grid.component';
import { DrawingGridService } from '../drawing-grid/drawing-grid.service';
import { PaintingMode, Pixel } from '../drawing-grid/interfaces';
import { Color } from '../global-interfaces/color.interface';

@Component({
  selector: 'ui-playground',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ColorPickerComponent,
    DrawingGridComponent,
  ],
  templateUrl: './playground.component.html',
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class PlaygroundComponent implements OnInit, OnDestroy {
  private readonly destroy$: Subject<void> = new Subject<void>();
  @Input() colors: Color[] = [];
  @Output() pixels: EventEmitter<Pixel[]> = new EventEmitter();

  width: number | undefined;
  height: number | undefined;
  pixelSize = 28;

  private paintingMode: PaintingMode = PaintingMode.CREATE;
  private color?: Color;

  constructor(private gridService: DrawingGridService) {}

  ngOnInit() {
    this.gridService.paintingMode$
      .pipe(takeUntil(this.destroy$))
      .subscribe((paintingMode) => {
        this.paintingMode = paintingMode;
      });

    this.width = document.documentElement.clientWidth - 120;
    this.height = document.documentElement.clientHeight - 200;
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onMouseDown(pixel: Pixel) {
    this.fillPixel(pixel.x, pixel.y);
  }

  onMouseMove(pixel: Pixel) {
    this.fillPixel(pixel.x, pixel.y);
  }

  onMouseUp() {
    this.pixels.emit(this.gridService.pixels);
  }

  onContextMenu(pixel: Pixel) {
    this.gridService.clearPixel(pixel.x, pixel.y);
  }

  private fillPixel(x: number, y: number) {
    if (this.paintingMode === PaintingMode.ERASE) {
      this.updateMaxAmountValues(x, y);
      this.gridService.clearPixel(x, y);
      return;
    }

    if (this.color && this.isAllowed(this.color)) {
      this.updateMaxAmountValues(x, y);
      this.gridService.fillPixel(x, y, this.color);
    }
  }

  private updateMaxAmountValues(x: number, y: number) {
    const pixel: Pixel | undefined = this.gridService.getPixel(x, y);

    if (pixel) {
      this.colors.forEach((item) => {
        if (item.maxAmount === undefined) {
          return;
        }

        if (pixel.fillStyle === item.value) {
          item.maxAmount++;
        }
      });
    }
  }

  private isAllowed(color: Color): boolean {
    if (color.maxAmount !== undefined) {
      if (color.maxAmount <= 0) {
        return false;
      } else {
        color.maxAmount--;
      }
    }

    return true;
  }

  selectColor(value: Color) {
    this.color = value;
  }
}
